html, body {
  width: 100%;
  height: 100%; }

body {
	background: #FFF;
	@include css-lock(16, 24, 600, 1000);
	line-height: 1.4 {
  width: 100%;
  height: 100%; } }

.tabs {
  &__nav {
    width: 100%;
    margin: 1em auto;
    text-align: center; }
  &__link {
    display: inline-block;
    padding: 0.25em 0.5em;
    text-transform: uppercase;
    color: #ddd;
    background-color: #396;
    border-radius: 3px;
    &:hover, &:focus, &:active {
      text-decoration: none; }
    &.is-active {
      background-color: #0c6;
      color: #fff; } }
  &__list {
    margin: 0 auto;
    max-width: 32em;
    overflow: hidden;
    padding: 1em;
 }    // background-color: #ddd
  &__content {
    position: relative;
    display: none;
    &.is-active {
      display: block; } } }
.center {
  @include vcenter; }
.slider, .popup-slider {
  @include clr;
  width: 30em;
 }  // overflow: hidden
.slide {
  margin: 0 0.5em;
  padding: 1em 0.5em;
  position: relative;
  float: left;
  display: block;
  background-color: #396;
  min-height: 500px;
  border-radius: 3px;
  overflow: hidden;
  &__img {
    width: 100%;
    height: 300px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
  &__text {
    display: none;
    font-size: 16px; }

  &__title {
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    margin: 0.5em 0; }

  &__about {
    text-align: center;
    font-size: 16px; }
  &__btn {
    display: block;
    width: 100%;
    padding: 0.5em;
    background-color: rgba(204, 51, 0, 0.51);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    text-transform: uppercase;
    transition: 0.3s;
    &:hover {
      background-color: rgba(204, 51, 0, 1); } } }

.popup-container {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8); }
.popup-box {
  @include vcenter;
  @include clr;
  display: block;
  min-width: 600px;
  min-height: 500px;
  background-color: #FFF;
 }  // position: relative
.popup-slider {
  // width: auto
  position: relative;
  .slide {
    margin: 1em;
    @include clr;
    &__img {
      position: relative;
      float: right;
      width: 50%;
      max-height: 100%;
      min-height: 450px;
      background-color: #FFF; }

    &__title {
      position: relative;
      text-align: left;
      margin: 1em;
      &:before {
        content: "modal "; } }
    &__text {
      position: relative;
      display: block;
      margin-left: 1em; }
    &__about {
      text-align: left;
      position: relative;
      margin: 1em; }
    &__btn {
      display: none; } } }
.popup-close-btn {
  display: block;
  width: 25px;
  height: 25px;
  position: absolute;
  z-index: 999;
  top: 0;
  right: 0;
  color: red;
  background-color: transparent;
  &:before, &:after {
    content: "";
    display: block;
    border: 2px solid red; }
  &:before {
    transform: rotate(45deg) translate(1px, 1px); }
  &:after {
    transform: rotate(-45deg) translate(2px, -2px); } }
.popup-slide-counter {
  text-align: center;
  font-size: 16px;
  position: absolute;
  z-index: 99;
  top: 85%;
  right: 5%;
  width: 50px;
  height: 20px;
  background-color: lightblue; }

.slick-prev {
  height: 100px;
  width: 40px;
  margin-top: -40px;
  &:before {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    border: 5px solid red;
    border-top-color: transparent;
    border-right-color: transparent;
    border-radius: 3px;
    transform: translateX(10px) rotate(45deg); } }
.slick-next {
  height: 100px;
  width: 40px;
  margin-top: -40px;
  &:before {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    border: 5px solid red;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-radius: 3px;
    transform: translateX(10px) rotate(45deg); } }
